<template>
<header class="header">
  <div class="container">
    <div class="header__body">
      <div class="header__logo"><router-link :to=" { name: 'Home' } ">
        <picture>
          <source srcset="../../public/static/images/home/L1.webp">
          <img src="../../public/static/images/home/L1.png" alt="">
        </picture>
      </router-link></div>
      <button class="header__burger" @click="headerUpdate ()" v-bind:class="{ active: headerState }"><span></span></button>
      <nav class="header__menu" v-bind:class="{ active: headerState }">
        <ul class="header__list">
          <li class="header__link">
            Features
          </li>
          <li class="header__link">
            Screenshots
          </li>
          <li class="header__link">
            Testimonials
          </li>
          <li class="header__link">
            Pricing
          </li>
        </ul>
      </nav>
    </div>
  </div>
</header>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  name: "AppHeader",
  computed: {
    ...mapGetters([
        'headerState'
    ])
  },
  methods: {
    headerUpdate () {
      this.toggleHeaderState()
    },
    ...mapActions(['toggleHeaderState'])
  }
}
</script>

<style scoped lang="scss">
.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 23;
  margin-top: 30px;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 21;
  }
  &__body {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 40px;
    padding: 10px 0;
    justify-content: flex-start;
    @media (max-width: 997.98px) {
      justify-content: space-between;
    }
  }
  &__logo {
    z-index: 22;
    flex: 0 0 120px;
    max-width: 120px;
    overflow: hidden;
    height: 40px;
    img {
      max-width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
      object-position: center;
    }
  }
  &__list {
    display: flex;
    position: relative;
    z-index: 22;
    @media (max-width: 997.98px) {
      flex-direction: column;
    }
    li {
      margin-right: 2.5rem;
      &:last-child {
        margin-right: 0;
      }
      @media (max-width: 997.98px) {
        margin-right: 0;
        margin-bottom: 3rem;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &__link, &__logo a {
    color: #fff;
    font-size: 1.8rem;
  }
  &__burger {
    display: none;
    @media (max-width: 997.98px) {
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
      border: none;
      background-color: transparent;
      z-index: 22;
      span {
        overflow: visible;
        position: absolute;
        width: 100%;
        height: 2px;
        top: 50%;
        left: 0;
        transform: translateY(-50%) scale(1);
        background-color: #fff;
        opacity: 1;
        transition: .2s linear all;
      }
      &:before, &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        background-color: #fff;
        transition: .2s linear all;
      }
      &:before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
      &.active {
        span {
          transform: translateY(-50%) scale(0);
          opacity: 0;
        }
        &:before {
          top: 50%;
          transform: rotate(-45deg) translateY(-50%);
        }
        &:after {
          bottom: 50%;
          transform: rotate(45deg) translateY(50%);
        }
      }
    }
  }
  &__menu {
    @media (max-width: 997.98px) {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      position: fixed;
      background-color: rgba(0,0,0,.6);
      padding: 100px 20px 30px 20px;
      box-sizing: border-box;
      overflow: auto;
      opacity: 0;
      transition: .2s linear all;
      &.active {
        opacity: 1;
      }
    }
  }
}
</style>
